.loader-container {
  display: none;
}

.loader {
  width: 16px;
  height: 16px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  margin: 0;
  font-weight: bold;
  display: none;
}

.show-error {
  display: block;
}

.container {
  flex-direction: row;
  gap: 24px;
  display: flex;
}

.cat-image {
  width: 400px;
  max-height: 400px;
}

